body::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,.3);box-shadow:inset 0 0 6px rgba(0,0,0,.3);background-color:#343a40}body::-webkit-scrollbar{width:6px;background-color:#343a40}body::-webkit-scrollbar-thumb{background-color:#000}.fullscreen h1.title{margin-bottom:0;letter-spacing:2px}.undertitle{font-style:italic}.box-container p{font-size:18px;margin-bottom:.2em}.info-button{cursor:pointer}.info-container{padding-left:2.5em}.btn{border-radius:0}.image-circle{border-radius:50%}.naruto-run{background-image:url(https://i.imgur.com/pyR0NvG.gif)}.konoha{background-image:url(https://i.imgur.com/YcE5a8R.png)}.sharingan{background-image:url(https://i.imgur.com/MCzJF4G.gif);vertical-align:-.3em;height:1.25em;width:1.25em;background-size:1.25em 1.25em}.avatar-container img.image-title:hover{animation:color-rotate 1s;animation-iteration-count:infinite;animation-direction:alternate}@media (max-width:550px){.info-container{padding-left:0}}@keyframes color-rotate{from{filter:hue-rotate(0)}to{filter:hue-rotate(360deg)}}